/*-------------------------------
		2.-MODULES
--------------------------------*/


/*--------- MAIN NAV ----------*/

nav {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 100;
	height: 85px;
	opacity: 0;
    -webkit-transition: background-color .1s ease-in, opacity .1s ease-in;
    -o-transition: background-color .1s ease-in, opacity .1s ease-in;
    -moz-transition: background-color .1s ease-in, opacity .1s ease-in;
	transition: background-color .1s ease-in, opacity .1s ease-in;



	//Tablet
	@media(max-width: $tablet){
		height: auto;
	}
	//Mobile
	@media(max-width: $mobile){
		z-index: 999;
	}

	&.home-default{
		opacity: 1;
		background-color: $primary-color;
	}
	&.home-nav{
		background-color: transparent;
		.nav-container{
			border-bottom: 1px solid $white;
		}
	}
	&.home-scroll{
		background-color: $primary-color;
		.nav-container{
			border-bottom: 1px solid transparent;
		}
	}
	&.home-mobile{
		opacity: 0;
	}
	&.home-mobile-scroll{
		opacity: 1;
	}
	

	&.explorer-nav-active{
		background-color: $white;
	}

	&.secondary-nav {
		background-color: $primary-blue;
		padding: 0;
		margin: auto;
		height: 70px;

		//Tablet
		@media(max-width: $tablet){
			padding: 0px 15px;
			height: auto;
		}

		// Mobile
		@media(max-width: $mobile){
			padding: 0 15px;
		}
		.container {
			border-bottom: 1px solid $white;
			padding: 13px;
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}

	.nav-container{
		width: 96.5%;
		height: 75px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 10px;
		border-bottom: 1px solid transparent;
		transition: border-bottom .1s ease-in;
		

	}
	.hamburger-menu, .search-icon {
		cursor: pointer;
		transition: opacity .3s;
		&:hover {
			opacity: .8;
		}
	}

	.close-btn{
		display: none;
	}

	.logo-big {
		height: 40px;
		width: auto;
	}

	.logo-small {
		width: 45px;
	}
	.logo-large{
		width: 260px;
		padding-top: 20px;
	}

	.sub-menu {
		background-color: $transparent-background;
		position: fixed;
		top: 84px;
		left: 0;
		padding: 0;
		height: 60px;
		width: 100%;
		transition: left .3s ease-in-out;
		
		//Tablet
		@media(max-width: $tablet){
			top: 75px;
		}


		div {
			padding: 0;
		}
		h3 {
			color: $maroon;
			display: inline-block;
			font-weight: $semibold;
			font-size: 18px;
			margin: 0 10px;

			// //Tablet
			// @media(max-width: 1090px){
			// 	display: none;
			// }

			//Mobile
			// @media(max-width: $mobile){
			// 	display: none;
			// }
		}
		.explore{
			margin: 0 10px;
			cursor: pointer;
		}
		a,
		.explore-text {
			color: $maroon;
			display: inline-block;
			font-size: 18px;
			font-weight: $medium;
			margin: 0;

			&.active {
				color: $primary-pink;
				font-weight: $semibold;
				border-bottom: 4px solid $primary-pink;
			}
			
			&.explore-text {
				color: $primary-pink;
				font-weight: $semibold;
			}

			//Tablet
			@media(max-width: $tablet){
				font-size: 14px;
				display: inline-block;
			}

			//Mobile
			@media(max-width: $mobile){
				display: none;

				&.active {
					display: block;
					color: $primary-color;
					font-size: 24px;
					border: none;
					margin: auto;
				}
				&.explore-text {
					display: none;
				}
			}
		}

		img {
			align-self: center;	

			//Mobile
			@media(max-width: $mobile){
				display: block;
			}
		}
		#top{
			li{
				display: inline-block;
				&:after{
					content: ' ';
					margin: 0 10px;
					padding: 0 5px;
					vertical-align: sub;
					background: url(../img/icons/arrow-right.svg); 
					background-repeat: no-repeat;
				}
				a{

					@media (max-width: $tablet){
						font-size: 13px;
					}
				}

				//Mobile
				@media (max-width: $mobile){
					display: none;
				}
				&.active{
					display: inline-block;
				}
			}
			.current-menu-item{
				a{
					color: $primary-pink;
					font-weight: $semibold;
					border-bottom: 3px solid $primary-pink;
					display: inline-block!important;
					line-height: 3.15em;
				}

				// Tablet
				@media(max-width: $tablet){
					a {
						line-height: 3;
					}
				}
				//Mobile
				@media (max-width: $mobile){
					
					display: inline-block!important;
					padding-left: 40px;
					&:after{
						display: none;
					}
				}
			}
			.current_page_item{
				a{
					color: $primary-pink;
					font-weight: $semibold;
					border-bottom: 4px solid $primary-pink;
					display: inline-block!important;
				}
				//Mobile
				@media (max-width: $mobile){
					display: inline-block!important;
					padding-left: 40px;
					&:after{
						display: none;
					}

					a {
						border-bottom: none;
						font-size: 20px;
					}
				}
			}
			//Mobile
			@media (max-width: $mobile){
				width: 100%;
				text-align: center;
			}
		}
		.container{
			display: flex;
			align-items: center;
			justify-content: space-between;
			max-width: 1300px;
		}
		.menu-top-menu-container{
			width: 67%;
			// Tablet
			@media(max-width: $tablet){
				margin-left: 10px;
				width: 87%;
			}
			//Mobile
			@media (max-width: $mobile){
				width: 100%;
			}
		}
	}

	//Explore Menu	
	.explore-menu{
		background-color: $primary-color;
		display: none;
		padding-bottom: 110px;
		height: 100vh;
		overflow: auto;
		width: 100%;
		position: absolute;
		z-index: 15;

		//Custom BP
		@media (max-height: $mobile){
			.cross{
				bottom: 3px;
				width: 80px;
				position: relative;
			}
		}

		//Mobile
		@media (max-width: $mobile){
			padding-bottom: 85px;
		}

		div{
			display: block;
		}
		.row{
			display: flex;
			padding: 0;
			// Mobile
			@media (max-width: $mobile){
				padding: 0 15px;
			}
		}

		.title{
			h2{
				font-size: 18px;
				color: $white;
				padding: 26px 0;
				border-bottom: 1px solid $explore-color;
			}
		}

		.content{
			padding-top: 60px;

			.container-explore{
				padding: 0;
				max-width: 600px;
				margin: auto;
				//Mobile
				@media (max-width: $mobile){
					max-width: initial;
					margin: 0;
				}
				.element{
					//Mobile
					@media (max-width: $mobile){
						margin-bottom: 25px;
					}
					.circle-container{
						display: flex; 
						justify-content: center;
						padding: 0;
						margin-bottom: 10px;
						position: relative;
						z-index: 1;
						margin-right: 40px;
						flex-flow: column;
						justify-content: center;
						margin-bottom: 0;

						.circle{
							height: 55px;
							width: 55px;
							border-radius: 50%;
							border: 2px solid $explore-color;
							display: flex;
							align-items: center;
							justify-content: center;
							p{
								color: $white;
							}
						}
						img{
							margin: 20px 0;
						}
					}
					.active{
						background-color: $teal;
						border: 0 !important;
					}
					.content-text{
						padding-bottom: 35px;
						border-bottom: 1px solid $explore-color;

						//Mobile
						@media (max-width: $mobile){
							width: 100%;
							height: 100%;
							padding: 0;
							border-bottom: 0;
						}
		
						h3, p{
							color: $white;
						}
						h3{
							font-size: 28px;
							margin-bottom: 10px;

							//Tablet
							@media (max-width: $mobile){
								font-size: 20px;
								margin-bottom: 0px;
							}
						}
						p{
							font-size: 16px;
						}
						&.last{
							border: 0;
						}
					}
					&.second-line{
						margin-top: 0px;
					}
				}
				//Rows
				.arrows{
					position: absolute;
					bottom: 145px;

					img{
						position: relative;
						width: 110px;
					}
					.arrow-1{
						left: 170px;
						bottom: -8px;
					}
					.arrow-2{
						left: 248px;
						bottom: 4px;
					}
					.arrow-3{
						left: 270px;
						bottom: 0px;
					}
					.arrow-4{
						left: 350px;
						bottom: -4px;
					}
				}
			}
		}

		.close-button {
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
	.cross{
		cursor: pointer;
		margin-bottom: 10px;
		width: 85px;

		//Tablet
		@media (max-width: $tablet){
    		bottom: 102px;
		}

		//Mobile
		@media (max-width: $mobile){
			display: none;
		} 
	}
}

// SideBar

.side-nav {
	position: fixed;
	left: -25%;
	top: 0;
	height: 100vh;
	width: 25%;
	background-color: $primary-color;
	z-index: 100;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	box-shadow: 0 0 10px rgba(0,0,0,0.2);	
	transition: left .3s ease-in-out;

	//Tablet
	@media (max-width: $tablet){
		left: -50%;
		width: 50%;
	} 
	//Mobile
	@media (max-width: $mobile){
		left: -100%;
	} 

	.close-btn {
		position: fixed;
		top: 25px;
		left: -25%;
		cursor: pointer;
		transition: left .3s ease-in-out;
		&:hover {
			opacity: .8;
		}
		
	}

	ul {
		padding: 0 50px;
		margin-top: 100px;

		@media (max-height: 768px){
			padding: 0 30px 25px 30px;
			margin-top: 80px;
		}

		// Mobile
		@media(max-width: $mobile){
			margin-top: 70px;
			padding: 0 16px;
		}
		
		li {
			color: $white;
			padding: 10px 0;
			border-bottom: 1px solid $transparent-white;
			a {
				color: $white;
				padding-left: 5px;
				font-size: 1.2em;

				@media (max-height: 768px){
					font-size: 1em;
					line-height: 1em;
				}

				&.active{
					color: $teal;
				}
			}
		}
	}

	.newsletter-nav {
		// /height: 40%;
		background: #392895;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 50px;

		@media (max-height: 768px){
			padding: 30px;
		}

		// Mobile
		@media(max-width: $mobile){
			padding: 16px;
		}

		img {    
			width: 70px;
			margin: auto;
			margin-bottom: 25px;
			@media (max-height: 768px){
				width: 60px;
				margin-bottom: -5px;
			}
			// Mobile
			@media(max-width: $mobile){
				margin-bottom: -5px;
			}
		}

		h1 {
			color: $white;
			font-size: 24px;
			margin: 15px 0;
			@media (max-height: 768px){
				font-size: 20px;
			}
			// Mobile
			@media(max-width: $mobile){
				font-size: 20px;
			}

		}
		p {
			color: $white;
			@media (max-height: 768px){
				font-size: 12px;
				line-height: 1.5em;
			}
			// Mobile
			@media(max-width: $mobile){
				display: none;
			}
		}

		input[type=text], input[type=email] {
			border: none;
			border-bottom: 1px solid $transparent-white;
			background: transparent;
			margin-bottom: 20px;
			color: $white;
			width: 100%;
		}

		input[type=email]{
			padding-top: 15px;
			&::placeholder{
				text-align: center;
				color: $white;
			}
		}

		.btn {
			display: inline-block;
			width: 128px;
			font-size: 16px;
		}

		input[type=submit]{
			padding: 10px 25px;
			background-color: $teal;
			color: $white;
			border-radius: 50px;
			font-weight: $medium;
		}
	}
}

/*--------- // MAIN NAV ----------*/






/*--------- SECONDARY HEADER ----------*/

.secondary-header {
	background-color: $primary-blue;
	padding: 150px 0 100px 0;
	>div {
		display: flex;
		width: 65%;

		// Tablet
		@media(max-width: $tablet){
			width: 80%;
		}
		// Mobile
		@media(max-width: $mobile){
			flex-direction: column;
			text-align: center;
		}
	}
	img {
		width: 30%;

		// Mobile
		@media(max-width: $mobile){
			margin: auto;
			width: 70%;
		}
	}

	.page-title {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-left: 80px;
		// Mobile
		@media(max-width: $mobile){
			padding-left: 0px;
		}
		h1 {
			color: $white;
			font-size: 35px;
			margin-bottom: 15px;
			//Mobile
			@media(max-width: $mobile){
				margin-top: 25px;
				font-size: 30px;
			}
		}

		p {
			color: $white;
		}
	}
	
}

/*--------- END SECONDARY HEADER ----------*/


/*--------- VIDEO MODULE ----------*/
.video-playlist-container{
	.video-module {
		.video-wrapper-section {
			width: 100%;
			position: relative;
			padding-bottom: 56.25%;
			overflow: hidden;
			z-index: 1;
		
			.video {
				position: absolute;
				height: 100%;
				width: 100%;
				left: 0;
				top: 0;
				z-index: 11;
			}
			button {
				//btn close video
				position: absolute;
				bottom: 25px;
				right: 25px;
				z-index: 15;
				cursor: pointer;	
				//Mobile
				@media(max-width: $mobile){
					width: 40px;
					height: 40px;
				}		
			}
		}
		
		.video-placeholder {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			z-index: 2;

			span {
				position: absolute;
				bottom: 0;
				left: 0;
				padding: 20px 50px;
				background: $transparent-white;

				//Mobile
				@media(max-width: $mobile){
					width: 100%;
					bottom: -60px;
					text-align: left;
					padding: 20px 15px;
					border-bottom: 1px solid $border-color;
				}
			}
		
		}
		.play-video-btn, .close-video-btn{
			img{
				width: 30%;
				@media screen and (min-width:0\0) { 
					/* Styles for IE9, IE10, IE11  */
					height: 100%;
				}
			}
		}

		.slider_video_header{
			.blog-thumbnail{
				position: relative;
				h1{
					font-size: 18px; 
					font-weight: $medium;
				}
				.play-video-btn{
					top: 100px;
					height: 40px;
					width: 40px;
					right: -13px;
					position: absolute;
					cursor: pointer;
					img{
						width: 10px;
						@media screen and (min-width:0\0) { 
							/* Styles for IE9, IE10, IE11  */
							height: 100%;
						}
					}
				}
			}
		}
	}

}



/*--------- END VIDEO MODULE ----------*/


/*--------- VIDEO TRANSCRIPT ----------*/
#video-trascript{
	//Tablet
	@media (max-width: $tablet){
		margin: 15px;
		h1{
			font-size: 23px !important;
		}
		#tanscription{
			padding: 0 !important;
			margin-top: 20px !important;
		}
	}
}

.inactive{
	border-bottom: 1px solid $baby-blue !important;
	color: $baby-blue !important;
}

/*--------- END VIDEO TRANSCRIPT ----------*/

/*--------- FOOTER NAVBAR ----------*/


.footer-navbar{
	display: none;
	flex-direction: row;
	width: 100%;
	z-index: 5;
	margin-top: 50px;
	position: relative;

	//Tablet
	@media(max-width: $tablet){
		.container{ 
			width: 100%;
			max-width: 100%;
			padding: 0;
		}
	}
	
	//Mobile
	@media(max-width: $mobile){
		position: relative;
	}

	.box-container {
		display: flex;
		flex-direction: row;
		box-shadow: $shadow;
		margin-bottom: -50px;

		// Mobile
		@media(max-width: $mobile){
			flex-direction: column;
			margin-bottom: 0;
		}
		.menu-footer-container{
			width: 100%;
			#footer {
				display: flex;
				height: 100%;

				//Mobile
				@media(max-width: $mobile){
					flex-direction: column;
				}

				li {
					min-height: 145px;
					height: 100%;
					padding: 20px 40px;
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					align-items: flex-start;
					background-color: $white;
					width: 25%;

					//Tablet
					@media(max-width: $tablet){
						padding: 20px;
					}


					//Mobile
					@media(max-width: $mobile){
						width: 100%;
						text-align: left;
						flex-direction: column;

						&:first-child {
							display: flex;
						}
					}


					&:nth-child(even){
						border: 1px solid $off-white;
					}

					h2 {
						color: $black;
						font-size: 18px;
						font-weight: $medium;
						line-height: 1.5em;
						padding-bottom: 10px;

						//Tablet
						@media(max-width:$tablet) {
							font-size: 14px;
							line-height: 1.3em;
						}
						//Mobile
						@media(max-width:$mobile) {
							font-size: 18px;
						}
					}

					a {
						color: $primary-pink;
						border-bottom: 1px;
						border-style: solid;
						flex-wrap: wrap;
						font-weight: $medium;
						padding-bottom: 5px;
					}
				}
			}
		}
		//Mobile
		@media(max-width: $mobile){

		}
	}

}

/*--------- END FOOTER NAVBAR ----------*/


/*--------- FOOTER ----------*/

footer{
	background: $primary-blue;
	padding: 80px 0;
	position: relative;


	//Tablet
	@media(max-width: $tablet){
		>div{
			padding: 0 15px !important;
		}
	}

	//Mobile
	@media(max-width: $mobile){
		padding: 35px 20px;
	}

	.logo {
		margin-bottom: 25px;
	}

	p {
		color: $white;
		width: 100%;

		//Mobile
		@media(max-width: $mobile){
			width: 100%;
		}
		.footer-a{
			color: $teal;
		}
	}

	.copy {
		display: flex;
		flex-direction: row;
		border-top: 1px solid $white;
		padding-top: 30px;
		margin-top: 25px;

		// Tablet
		@media(max-width: $tablet){
			padding-top: 30px !important;
		}

		//Mobile
		@media(max-width: $mobile){
			flex-direction: column-reverse;
			border-top: none;
		}

		span {
			color: $white;
		}

		a {
			color: $teal;
			padding-left: 10px;

			// Tablet
			@media(max-width: $tablet){
				padding-left: 10px;
			}
			
			//Mobile
			@media(max-width: $mobile){
				padding: 10px 0;
			}
		}
	}
	ul {
		margin-left: auto;
		margin-top: -5px;

		//Mobile
		@media(max-width: $mobile){
			margin: 25px 0; 
			text-align: center;
		}

		li {
			display: inline-block;
			
			//Mobile
			@media(max-width: $mobile){
				margin: 0 5px;
				img{
					width: 42px;
				}
			}
		}
	}
}

/*--------- END FOOTER ----------*/


