/*--------- RECENT BLOG POSTS ----------*/

.blog-posts-preview-container {
	background-color: $off-white;
	background: linear-gradient(to bottom, $white 0%,$white 20%,$white 40%,$off-white 30%,$off-white 100%); /* W3C */
    
	padding: 50px 0;
    position: relative;
    
    // Mobile
    @media(max-width: $mobile){
        background: linear-gradient(to bottom, $white 0%, $white 17%, $off-white 17%, $off-white 100%);
    }

	.header {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 25px 0px;
        //Tablet
        @media(max-width: $tablet){
            padding: 25px 10px;
        }
		h1 {
            font-size: 30px;
            //Tablet
            @media(max-width: $tablet){
                font-size: 23px; 
            }
		}

		a {
			color: $baby-blue;
            align-self: center;
            border-bottom: 1px solid;
            padding-bottom: 5px;

			//Mobile 
			@media(max-width: $mobile){
				display: none;
			}
			
		}
	}
	.body {
		display: flex;
        justify-content: space-between;
        align-items: stretch;
        margin: 0 -10px 0 -10px;

		//Mobile
        @media(max-width: $mobile){
			flex-direction: column;    
        } 
    }
    .footer-view {
        display: flex;
        justify-content: center;
        .view-all {
            cursor: pointer;
            font-size: 26px;
            color: #4a90e2;
            display: inline-block;
            border-bottom: 1px solid #4a90e2;
            padding-bottom: 5px;
        }
    }
}


/*--------- END RECENT BLOG POSTS ----------*/

/*--------- BLOG POST COMPONENT ----------*/

.blog-post-preview {
    background-color: $white;
    display: flex;
    flex-direction: column;
    margin: 0 10px;
    max-width: 357px;
    flex: 1 1 0;
    align-items: stretch;

    //Mobile
    @media(max-width: $mobile){  
        flex-basis: auto;
        flex-grow: unset;
        margin: 20px;
        max-width: none;
    }

    a{
        max-height: 225px;
        overflow: hidden;

        //Mobile
        @media(max-width: $mobile){
            max-height: 213px;
        }
    }
    
    .post-thumbnail {
        width: 100%;
       
    }
    .content{
        padding: 25px;
        flex: 1 1 0;
        padding-bottom: 0;
        h3{
            color: $teal;
            font-size: 1.1rem;
            padding-bottom: 15px;
            font-weight: $light;
        }
    }
    .footer{
        padding: 25px;
    }
    
    h2 {
        color: $black;
        font-size: 18px;
        flex-grow: 1;
        font-weight: $regular;
        line-height: 1.5em;
    }

    h3 {
        color: $teal;
        font-size: 1.1rem;
        padding-bottom: 15px;
        font-weight: $light;
    }

    .footer {
        width: 100%;
        display: flex;

        .post-author {
            width: 49px;
            margin-right: 10px;
            border: 2px solid $white;
            border-radius: 100%;
            background-color: $white;
            img{
                width: 100%;
                height: auto;
            }
        }

        span{
            color: $black;
            align-self: center;
            font-size: 15px;
        }
    }
    @media screen and (min-width:0\0) { 
        /* Styles for IE9, IE10, IE11  */
        width: 32%;
        .footer{
            height: 100px;
            position: relative;
            .post-author{
                bottom: 20px;
                position: absolute;
            }
            span{
                position: absolute;
                bottom: 34px;
                left: 90px;
            }
        }
    }
}

/*--------- END BLOG POST COMPONENT ----------*/