/*--------- SPONSORS MODULE ----------*/

.sponsors {
	margin: 50px auto;
	background-color: $baby-blue;
	padding: 100px;
	
	//Tablet
	@media(max-width: $tablet){
		padding: 30px;
	}
	//Mobile
	@media(max-width: $mobile){
		text-align: center;
	}
	.description{
		padding-right: 27px;
		//Mobile
		@media(max-width: $mobile){
			margin-top: 30px;
		}
	}

	span {
		color: $white;
		font-weight: $medium;
		font-size: 15px;
	}

	h1 {
		color: $white;
		font-size: 50px;
		font-weight: $regular;
		padding: 15px 0;
    	padding-bottom: 24px;
		// Tablet
		@media(max-width: $tablet){
			font-size: 30px;
		}
		// Mobile
		@media(max-width: $mobile){
			font-size: 24px;
		}
	}

	p {
		color: $white;
		font-size: 16px;
	}

	a {
		color: $white;
		display: inline-block;
		padding-top: 25px;
		border-bottom: 1px solid $white;
		padding-bottom: 5px;
		font-weight: $medium;
		font-size: 17px;
	}
	&.colored-sponsor{
		.video-module {
			margin: 0;
		
			.video-placeholder {
				button {
					position: absolute;
					bottom: 50%;
					right: 50%;
					width: 80px;
					height: 80px;
					margin-right: -40px;
					margin-bottom: -40px;
				}
			
			}
		}
	}
}

/*--------- END SPONSORS MODULE ----------*/