/*--------- HOME SLIDER ----------*/

.home-slider {
	width: 100%;
	background-size: cover;
	background-position: center center;
	position: relative;


	//Tablet
	@media(max-width: $tablet){
		// margin-top: 30px;
		min-height: auto;
	}
	//Mobile
	@media(max-width:$mobile) {
		background-image: none !important;
		min-height: auto;
		margin-top: 75px;
		> img {
			width: 100%;
		}
	}

	.bg-video {
		position: relative;
	}

	.container {
		position: relative;
	}
	
	.container-video{
		max-width: 1440px;
		margin: auto;
		background-color: $off-white;
		margin-bottom: 140px;

		//tablet
		@media (max-width: $tablet){
			margin-bottom: 0;
		}
		// //Mobile
		// @media (max-width: $mobile){
		// 	padding-top: 44px;
		// }

	}

	.information-box-container {
		.bg-container{
			position: absolute;
			top: 0;
			left:0;
			height: 100%;
			width: 100%;
			padding-top: 100px;
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			z-index: 3;	
		}

		//Mobile
		@media(max-width:$mobile) {
			margin: 0;
			padding-top: 0;
		}
	}

	.information-box {
		position: absolute;
		bottom: $position-home;
		z-index: 11;
		margin-left: 5%;
		
		//Tablet
		@media(max-width:$tablet) {
			width: 380px;
			bottom: $position-home-tablet;
			h1{
				font-size: 30px;
			}
			p{
				padding-bottom: 0;
				font-size: 12px;
			}
		}

		//Mobile
		@media(max-width:$mobile) {
			margin: 0;
			width: 100%;
			position: static;
		}
	}

	.play-video-btn, .close-video-btn {
		position: absolute;
		right: 5%;
		bottom: $position-home;
		cursor: pointer;
		z-index: 11;
		//Tablet
		@media(max-width: $tablet){ 
			bottom: $position-home-tablet;
			width: 51px;
			height: 51px;
		}
		@media screen and (min-width:0\0) { 
			/* Styles for IE9, IE10, IE11  */
			img{
				height: 100%;
			}
		}
	}

	.home-slider-footer {
		display: flex;
		flex-direction: row;
		position: absolute;
		bottom: -70px;
		width: 100%;
		z-index: 5;

		//Tablet
		@media(max-width: $tablet){
			position: static;
			margin-bottom: 50px;

			.container{ 
				width: 100%;
				max-width: 100%;
				padding: 0;
			}
		}
		
		//Mobile
		@media(max-width: $mobile){
			position: relative;
			display: none;
		}

		.box-container {
			display: flex;
			flex-direction: row;
			box-shadow: $shadow;
			> div {
				min-height: 145px;
				padding: 30px 40px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				background-color: $white;
				width: 25%;

				//Tablet
				@media(max-width: $tablet){
					padding: 20px;
				}


				//Mobile
				@media(max-width: $mobile){
					width: 100%;
					display: none;
					text-align: center;

					&:first-child {
						display: flex;
					}
				}


				&:nth-child(even){
					border: 1px solid $off-white;
				}
	
				h2 {
					color: $black;
					font-size: 18px;
					font-weight: $medium;
					line-height: 1.5em;
					margin-bottom: 10px;

					//Tablet
					@media(max-width:$tablet) {
						font-size: 16px;
						line-height: 1.3em;
					}
					//Mobile
					@media(max-width:$mobile) {
						font-size: 26px;
					}
				}
	
				a {
					color: $primary-pink;
					border-bottom: 1px;
					border-style: solid;
					flex-wrap: wrap;
					font-weight: $medium;
					padding-bottom: 5px;
				}
			}

			//Mobile
			@media(max-width: $mobile){

			}
		}

	}
}

/*--------- END HOME SLIDER ----------*/

/*--------------- HEADER VIDEO -------------------*/

.blog-post-page-header{
    margin-top: 56px;
    .description{
        background-color: #fff;
		width: 50%;
		min-height: 25%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		cursor: pointer;
		//Tablet
		@media(max-width: $tablet){
			width: 50%;
		}
		//Mobile
		@media(max-width: $mobile){
			position: absolute;
			width: 100%;
			bottom: -95px;
			padding: 25px;
			height: 95px;
		}
		.centering{
			//Tablet
			@media(max-width: $tablet){
				width: 400px;
				right: 50px;
				left: 10px;
			}
			//Mobile
			@media (max-width: $mobile){
				left: 0px;
				width: 100%;
			}
		}
        h1{
            color: $black;
			font-size: 30px;
			font-weight: $light;
			margin-top: 5px;
			//Tablet
			@media(max-width: $tablet){
				font-size: 26px;
			}
			//Mobile
			@media(max-width: $mobile){
				font-size: 20px;
			}
		}
		&.description-static{
			//Mobile
			@media(max-width: $mobile){
				position: initial;
				width: 100%;
			}
		}
    }
    .play-video-btn, .close-video-btn{
		display: none;
		//bottom: 25px;
		bottom: 5%!important;
        height: 100px;
        position: absolute;
		right: 10% !important;
        width: 100px;
		cursor: pointer;
		//Tablet				
		@media (max-width: $tablet){
			right: 7% !important;
			width: 75px;
			height: 75px;
		}
		//Mobile				
		@media (max-width: $mobile){
			right: 7% !important;
			width: 40px;
			height: 40px;
		}
        img{
			width: 30%;
			@media screen and (min-width:0\0) { 
				/* Styles for IE9, IE10, IE11  */
				height: 100%;
			}
        }
    }
}

.slider_video_header{
    .blog-thumbnail{
        position: relative;
        .play-video-btn{
            top: 100px;
            height: 40px;
            width: 40px;
            right: -13px;
            position: absolute;
            cursor: pointer;
            img{
				width: 10px;
				@media screen and (min-width:0\0) { 
					/* Styles for IE9, IE10, IE11  */
					height: 100%;
				}
            }
        }
    }
}

/*--------- VIDEO WRAPPER ----------*/

.video-wrapper, .video-wrapper-playlist{
	width: 100%;
    position: relative;
    padding-bottom: 56.25%;
    overflow: hidden;
    min-width: 100%;
	min-height: 100%;
	z-index: 1;

	//Mobile
	@media(max-width: $mobile){
		padding-bottom: 0;
		min-height: auto;
	}
	
	.video {
		position: absolute;
		height: 100%;
		width: 100%;
		left: 0;
		top: 0;
		z-index: 11;
		//Mobile
		@media(max-width: $mobile){
			position: relative;
			min-height: 55vw;
		}
	}
}
/*--------- END VIDEO WRAPPER ----------*/
