/*-------------------------------
		1.-ELEMENTS
--------------------------------*/




.btn {
	padding: 10px 25px;
	background-color: $teal;
	color: $white;
	border-radius: 50px;
	font-weight: $medium;
}

.play-video-btn, .close-video-btn {
	background-color: rgba(255,255,255,.8);
	border: none;
	display: flex;
	justify-content: center;
	margin: auto;
	width: 100px;
	height: 100px;
	border-radius: 100%;
	z-index: 13;
	img {
		width: 30%;
		margin-left: 10%;
		@media screen and (min-width:0\0) { 
			/* Styles for IE9, IE10, IE11  */
			height: 100%;
		}
	}
}
.close-video-btn {
	img {
		width: 30%;
		margin-left: 0;
	}
}

.btn-download{
	color: $white;
	background-color: $primary-pink;
	font-family: $primary-font;
	font-size: 15px;
	padding: 15px;
	text-align: center;
}
.search-modal{
	display: none;
	height: 100vh;
	position: fixed;
	top: 0;
	width: 100vw;
	background: white;
	opacity: 0.95;
	left: 0;
	right: 0;
	overflow: hidden;
	padding-top: 7%;
	padding-left: 50px;
	z-index: 9999999999;
}

.slick-dots {
	display: flex!important;
	justify-content: center;
	
	margin: 0;
	padding: 1rem 0;
	
	list-style-type: none;
	
		li {
			margin: 0 0.6rem;
		}
	
		button {
			display: block;
			width: 8px;
			height: 8px;
			padding: 0;
			
			border: none;
			border-radius: 100%;
			background-color:$primary-grey;
			
			text-indent: -9999px;
		}
	
		li.slick-active button {
			background-color: $primary-pink;
		}
	
}

.styles-none{
	border-bottom: 0px!important;
	color: $black!important;
}
.link-to-home{
	font-size: 16px;
	text-align: center;
	a{
		padding-bottom: 3px;
		color: $primary-pink!important;
		border-bottom: 1px solid $primary-pink;
	}
}