/*--------- VIDEO WRAPPER ----------*/
.video-container{
	.title-video-container{
		text-align: left;
		margin: 15px 10px;
		.title-video{
			span{
				font-size: 25px;
				color: $primary-color;
			}
		}
	}
	.close-video-btn {
		display: none !important;
	}
}
.video-module, .video-module-section {
	position: relative;

	.video-wrapper-section, .video-wrapper-sponsor, .video-wrapper-playlist {
		width: 100%;
		//height: 100%;
		//height: 100vh;
		position: relative;
		padding-bottom: 56.25%;
		//pointer-events: none;
		overflow: hidden;
		min-width: 100%;
		min-height: 100%;
		z-index: 1;
	
		.video {
			position: absolute;
			height: 100%;
			width: 100%;
			left: 0;
			top: 0;
			z-index: 11;
		}
		button {
			//btn close video
			position: absolute;
			bottom: 5%;
			right: 7%;
			width: 90px;
			height: 90px;
			margin: 0;
			cursor: pointer;
			//Tablet
			@media(max-width: $tablet){
				width: 75px;
				height: 75px;
			}
			//Mobile
			@media(max-width: $mobile){
				width: 50px;
			height: 50px;
			}		
		}
	}
	
	.video-placeholder {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		z-index: 2;

		button {
			position: absolute;
			bottom: 5%;
			right: 7%;
			width: 90px;
			height: 90px;
			margin: 0;
			cursor: pointer;
			//Tablet
			@media(max-width: $tablet){
				width: 75px;
				height: 75px;
			}
			//Mobile
			@media(max-width: $mobile){
				width: 50px;
			height: 50px;
			}

			
		}
		.information-box{
			    position: absolute;
				bottom: 20%;
				z-index: 11;
				margin-left: 5%;
				width: 419px;
				.body {
					padding: 50px 65px;
				}
				span {
					font-size: 38px;
					color: $black;
					font-weight: $light;
				}
				p{
					font-weight: $medium;
					margin-top: 12px;
				}
				// Tablet
				@media(max-width: $tablet) {
					width: 380px;
   					bottom: 5%;
				}
		}
		// span {
		// 	position: absolute;
		// 	bottom: 0;
		// 	left: 0;
		// 	padding: 20px 50px;
		// 	background: $transparent-background;

		// 	//Mobile
		// 	@media(max-width: $mobile){
		// 		width: 100%;
		// 		bottom: -60px;
		// 		text-align: left;
		// 		padding: 20px 15px;
		// 		border-bottom: 1px solid $border-color;
		// 		font-weight: $semibold;
		// 	}
		// }
	
	}
}
/*--------- END VIDEO WRAPPER ----------*/