.clinical-trials-container{
    background-color: $white;
    background: none;

    %a-styles{
        font-size: 18px;
        border-bottom: 1px solid $baby-blue;
        padding-bottom: 3px;
        color: $baby-blue;
        //Tablet
        @media(max-width: $tablet){
            font-size: 17px;
        }
    }

    .header{
        //Mobile
        @media(max-width: $mobile){
            padding: 10px 15px;
        } 
        a{
            @extend %a-styles;
            text-decoration: none;
            //Mobile
            @media(max-width: $mobile){
                display: block;
            } 
        }
        h1{
            //Tablet
            @media(max-width: $tablet){
                font-size: 23px;
            }
        }
    }
    .clinical-trials-body{
        .clinical-content{
            padding: 0;
            padding-top: 25px;
            h2{
                color: $maroon;
                font-weight: $medium;
                font-size: 22px;
                margin-bottom: 10px;
                //Tablet
                @media(max-width: $tablet){
                    font-size: 16px;
                }
                //Mobile
                @media(max-width: $mobile){
                    font-size: 19px;
                }
            }
            p{
                font-size: 16px;
                //Tablet
                @media(max-width: $tablet){
                    font-size: 14px;
                }
                //Mobile
                @media(max-width: $mobile){
                    font-size: 16px;
                }
            }
        }
        .external-link{
            padding: 25px 0;
            a{
                @extend %a-styles;
                margin-right: 25px;
            }
        }
    }
}