/*--------- VIDEO WRAPPER ----------*/
.featured-video{
	padding: 50px 0;
		//Mobile
		@media(max-width: $tablet){
			padding: 50px 15px;
		}
	.featured-title{
		font-size: 30px;
		padding: 25px 0;
		//Mobile
		@media(max-width: $tablet){
			font-size: 23px;
		}
	}
	.featured-video-module, .video-module-section {
		position: relative;
	
		.video-wrapper-section-featured-video, .video-wrapper-sponsor, .video-wrapper-playlist {
			width: 100%;
			//height: 100%;
			//height: 100vh;
			position: relative;
			padding-bottom: 56.25%;
			//pointer-events: none;
			// overflow: hidden;
			min-width: 100%;
			min-height: auto;
			z-index: 1;

			// Mobile
			@media(max-width: $mobile) {
				padding-bottom: 127.25%;
			}
		
			.video {
				position: absolute;
				height: 100%;
				width: 100%;
				left: 0;
				top: 0;
				z-index: 11;
			}
			.close-video-btn {
				//btn close video
				position: absolute;
				bottom: 7%;
				right: 5%;
				width: 50px;
				height: 50px;
				margin: 0;
				cursor: pointer;
				display: none !important;
				//Tablet
				@media(max-width: $tablet){
					width: 75px;
					height: 75px;
				}
				//Mobile
				@media(max-width: $mobile){
					width: 50px;
					height: 50px;
				}		
			}
		}
		
		.video-placeholder {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			z-index: 2;
	
			button {
				width: 50px;
				height: 50px;
				margin: 0;
				cursor: pointer;
			}
			.category{
				position: absolute;
				top: -65px;
				right: 45px;
				width: 130px;
				height: 130px;
				display: flex;
				align-items: center;
				justify-content: center;
				background: $primary-pink;
				border-radius: 50%;
				p{
					font-size: 17px;
					color: $white;
				}
			}
			span {
				margin-right: 15px;
				font-weight: $semibold; // Tablet
				color: $white;
			}
			.title-and-button-container{
				position: absolute;
				bottom: 7%;
				right: 5%;
				display: flex;
				align-items: center;				
			}
		
		}
	}
}
/*--------- END VIDEO WRAPPER ----------*/