/*-------------------------------
		3.-LAYOUT
--------------------------------*/

/*---------  BLOG POSTS PAGE ----------*/

//Blog Post Header
.blog-post-page-header {
	position: relative;
	margin-top: 150px;
	img {
		width: 100%;
	}

	.description {
		position: absolute;
		left: 0;
		bottom: -2px;
		padding: 25px;
		background-color: $white;
		width: 50%;

		//Tablet 
		@media(max-width: $tablet){
			width: 60%;
		}

		// Mobile
		@media(max-width: $mobile){
			width: 100%;
			position: relative;
		}

		h1 {
			color: $primary-pink;
			font-size: 36px;

			// Tablet
			@media(max-width: $tablet){
				font-size: 26px;
			}
		}

		.footer {
			display: flex;
			margin-top: 20px;

			// Mobile
			@media(max-width: $mobile){
				display: none;
			}
			img {
				margin-right: 10px;
				margin-top: -5px;
				width: 40px;
				height: 40px;
			}

			span {
				padding-top: 5px;				
			}
			.author {
				padding-right: 10px;
				padding-left:  10px;
				border-right: 1px solid $border-color;
				position: relative;
			}

			.date {
				padding-left: 10px;
				position: relative;
			}
		}
	}
}

//Blog Post thumbnails
.blog-posts-page-container {
	background-color: $off-white;
	background: linear-gradient(to bottom, $white 0%,$white 18%,$white 18%,$off-white 18%,$off-white 100%); /* W3C */
	padding: 50px 0 80px 0;
	position: relative;
	margin-top: 50px;
	// Table
	@media(max-width: $tablet){
		padding-left: 15px;
		padding-right: 15px;
	}

	// Mobile
	@media(max-width: $mobile){
		padding-left: 0px;
		padding-right: 0px;
		background: linear-gradient(to bottom, $white 0%,$white 7%,$white 7%,$off-white 7%,$off-white 100%); /* W3C */
	}

	.header {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 25px 10px;

		h1 {
			font-size: 30px;
		}

		a {
			color: $baby-blue;
			text-decoration: underline;
			align-self: center;

			//Mobile 
			@media(max-width: $mobile){
				display: none;
			}
			
		}
	}
	.body {
		display: inline-block;
		display: flex;
		//justify-content: space-between;
		flex-wrap: wrap;

		// Mobile
		@media(max-width: $mobile){
			justify-content: space-between;
			padding: 0 15px;
		}

		.blog-post-preview {
			flex-grow: 0;
			flex-basis: auto;
			width: 32%;
			margin: 1%;
			&:first-child {
				margin-left: 0;
			}

			&:nth-child(3n + 1){
				margin-left: 0;
			}

			&:nth-child(3n + 3){
				margin-right: 0;
			}

			// Mobile
			@media(max-width: $mobile){
				width: 100%; 
				margin: auto;
				margin: 10px 0;
				
			}

			@media screen and (min-width:0\0) { 
				/* Styles for IE9, IE10, IE11  */
				.footer{
					height: 100px;
					position: relative;
					.post-author{
						bottom: 20px;
						position: absolute;
					}
					span{
						position: absolute;
						bottom: 34px;
						left: 90px;
					}
				}
			}
		}

		//Mobile
        @media(max-width: $mobile){
			flex-direction: column;   
        }

	}
}
/*--------- END BLOG PAGE ----------*/


/*---------  BLOG POST PAGE ----------*/
.blog-post-container {

	.blog-inner-container{
		width: 80%;
		margin: auto;
		/* Styles for IE9, IE10, IE11  */
		@media screen and (min-width:0\0) { 
			display: block;
			margin:0 auto;
			width: 1152px;
		}
		// Tablet
		@media(max-width: $tablet){
			width: 100%;
			padding-left: 15px !important;
			padding-right: 15px !important;
		}
	}
	.header {
		margin: auto;
		margin-top: 110px;

		// Mobile
		@media(max-width: $mobile){
			width: 100%;
			padding: 0 15px;
		}

		&.header-main{
			margin-top: 135px;
			
		}

		h1 {
			padding: 50px 0px; 
			border-bottom: 1px solid $border-color;
			font-size: 36px;
			font-weight: $light;
		}

		h2 {
			color: $teal;
			font-size: 1.6rem;
		}

		.details {
			display: flex;
			padding: 25px 0;

			.author {
				border-right: 1px solid $border-color;
				padding-right: 10px;
				font-size: 20px;
			}

			.date {
				padding-left: 10px;
				font-size: 20px;
			}

			span {
				padding-right: 10px;
				height: 25px;
				align-self: center;

				// Mobile
				@media(max-width: $mobile){
					font-size: 14px;
				}

			}
			.social-media {
				.norm_row, .sfsi_wDiv{
					position: static !important;
					text-align: right !important;
				}
				.sfsi_holders{
					display: none !important;
				}
				li {
					margin: 10px;

					// Mobile
					@media(max-width: $mobile){
						img {
							width: 25px;
						}
					}
				}
			}
		}
	}
	.header-blog-super-title{
		margin: auto;
		margin-top: 55px!important;
		text-align: center;
		width: 70%;
		h1{
			border: none;
			padding: 50px 0px 10px 0px;
			font-size: 46px;
		}
		// Mobile
		@media(max-width: $mobile){
			width: 100%!important;
			padding: 0 15px;
			h1{
				font-size: 32px;
			}
		}
	}

	.body {
		margin: auto;
		padding: 50px 0;

		// Mobile
		@media(max-width: $mobile){
			width: 100%;
			padding: 50px 15px;
		}

		h1 {
			font-size: 28px;
			font-weight: $medium;
			line-height: 1.5em;
		}

		h2 {
			font-size: 24px;
			font-weight: $medium;
			line-height: 1.5em;
		}
		h3 {
			font-size: 20px;
			font-weight: $medium;
			line-height: 1.5em;
		}

		h4 {
			font-size: 18px;
			font-weight: $medium;
			line-height: 1.5em;
		}


		p {
			line-height: 1.5em;
		}

		strong {
			font-weight: $semibold;
		}

		blockquote {
			p{
				color: $primary-pink;
				font-size: 26px;
			}
		}

		.container {
			padding: 0;
		}
	}
	.main-post-img {
		width: 100%;
		max-width: 100%;
	}

	.container-content {
		// display: flex;
		// justify-content: space-around;
		// align-items: stretch;

		.marketing {
			//Hidden for now. 
			flex: 0 0 300px;
			height: 600px;
			background-color: $border-color;
			display: none;

			// Tablet
			@media(max-width: $tablet){
				display: none;
			}
		}
	}


	//Author Data
	.author-footer {
		margin: auto;
		padding-bottom: 50px;
		display: flex;
		border-bottom: 1px solid $border-color;

		img {
			width: 100px;
			height: 100px;
			border-radius: 100%;

		}

		span {
			align-self: center;
			font-size: 26px;
			font-weight: $medium;
			margin-left: 25px;
		}
	}

	//Recomended Articles
	.recomended-articles {
		padding-top: 0;
		.header {
			margin-top: 0;
			h1 {
				border-bottom: none;
				padding: 0;
			}
		}

		.body {
			padding: 0;
			margin: 0;
			h2{
				font-weight: $regular;
			}
		}

		.footer{
			text-align: center;
			padding-top: 25px;
			@media screen and (min-width:0\0) { 
				/* Styles for IE9, IE10, IE11  */
				height: 100px;
				position: relative;
				.post-author{
					bottom: 20px;
					position: absolute;
				}
				span{
					position: absolute;
					bottom: 34px;
					left: 90px;
				}
			}

			a {
				border-bottom: 1px solid $baby-blue;
				padding-bottom: 4px;
			}
		}
	}
}
/*---------  END BLOG POST PAGE ----------*/



/*---------  VIDEO HUB PAGE ----------*/
.video-hub-page {

	.videohub-page-header {
		margin-top: 75px;
		.play-video-btn-container {
			width: 100%;
			min-height: 56vw;
			position: relative;
		}
		.play-video-btn {
			display: block !important;
			@media screen and (min-width:0\0) { 
				/* Styles for IE9, IE10, IE11  */
				img{
					height: 100%;
				}
			}
		}
		.video-placeholder {
			z-index: 12;
		}
		.description {
			position: absolute;
			left: 0;
			bottom: -2px;
			padding: 25px;
			background-color: $white;
			width: 50%;
			z-index: 15;
			//Tablet 
			@media(max-width: $tablet){
				width: 60%;
			}

			// Mobile
			@media(max-width: $mobile){
				width: 100%;
				position: initial;
			}

			h1 {
				color: $black;
				font-size: 30px;
				font-weight: 300;
				margin-top: 5px;

				// Tablet
				@media(max-width: $tablet){
					font-size: 26px;
				}
				// Mobile
				@media(max-width: $mobile){
					font-size: 20px;
				}
			}
		}
	}


	// Mobile
	@media(max-width: $mobile){
		.slider_video_header {
			display: flex !important;
			flex-direction: row !important;
			flex-wrap: wrap !important;
			margin: 0 15px;
			justify-content: space-between !important;

			.blog-thumbnail {
				display: flex;
				width: 45%;
				margin: 15px 0;

				h2 {
					padding-top: 5px;
				}

				.video {
					height: 100px;
				}

				.play-video-btn {
					top: 50px !important;
				}
			}
		}
	}
}



.video-hub-page-header {
	@extend .blog-post-page-header;
}
.videohub-page-header{
	margin-top: 0px;
	.blog-post-page-header{
		margin-top: 0px;
		//Tablet
		@media (max-width: $tablet){
			margin-top: 0px;
		}
	}
}

/*---------  END VIDEO HUB PAGE ----------*/

/*--------- VIDEO PLAYLIST -----------*/

.video-playlist-container {
	.blog-post-page-header{

		// Mobile
		@media(max-width: $mobile){
			margin-bottom: 100px;
		}
		.play-video-btn{
			display: block !important;
			@media screen and (min-width:0\0) { 
				/* Styles for IE9, IE10, IE11  */
				img{
					height: 100%;
				}
			}
		}
	}
}
#video-trascript{
	display: none;
	margin-bottom: 45px;
	h1{
		border-bottom: 1px solid $border-color;
		font-size: 36px;
		padding-bottom: 25px;
		#transcript{
			float: right;
			text-align: right;
			span{
				border-bottom: 1px solid $primary-pink;
				color: $primary-pink;
				cursor: pointer;
				font-size: 18px;
				vertical-align: middle;
			}
		}
	}
	#tanscription{
		display: none;
		line-height: 1.3;
		padding: 50px 20%;
		width: 100%;
		font-family: Arial, $primary-font;
	}
}

.blog-thumbnail, .blog-thumbnail-slide, .playlist-video{
	.blog-thumbnail-bg{
		height: 150px;
		position: absolute;
		top: 0px;
		width: 100%;
		background-size: cover;
	}

	.video{
		max-width: 100%;
		max-width: 100%;
		height: 150px;
	}
	p{
		display: none;
	}
	
	//Tablet
	@media (max-width: $tablet){
		.blog-thumbnail-bg, .video{
			height: 95px;
		}
	}
}
/*-------- END VIDEO PLAYLIST --------*/


/*---------  FAQ'S PAGE ----------*/
.faqs-page {
	.faqs-navbar {
		border-bottom: 1px solid $border-color;
		width: 100%;
		padding: 40px 0;
		margin-bottom: 50px;

		// Tablet
		@media(max-width: $tablet){
			padding: 40px 25px;
		}
		ul{
			// Tablet
			@media(max-width: $tablet){
				display: flex;
				align-items: center;
				overflow-x: auto;
			}
			li {
				display: inline-block;
				margin: 0 10px;
				padding: 10px 0;
				font-size: 20px;
				flex: auto 1 0;
				cursor: pointer;
				&:hover {
					color: $primary-pink;
				}
	
				&:first-child {
					margin-left: 0;
				}
	
				&:last-child {
					margin-right: 0;
				}
				&.active {
					color: $primary-pink;
					border-bottom: 1px solid $primary-pink;
					font-weight: $medium;
				}
			}
		}
	}
	.tab-group{
		padding-top: 30px;
	}
	.question {
		padding: 25px 0;
		h2 {
			font-size: 20px;
			font-weight: $medium;
			// Tablet
			@media(max-width: $tablet){
				margin-bottom: 20px;
			}
		}
		.answers{
			p{
				font-size: 16px;
			}
		}

		// Tablet
		@media(max-width: $tablet){
			padding: 25px;
		}
	}	
}
/*---------  END FAQ'S PAGE ----------*/



