/*-------- END APP REVIEWS ------------*/

/*-------------- EXPERT -------------*/
.experts-page{
	.secondary-header{
		//Tablet
		@media(max-width: $tablet){
			padding-left: 25px;
			padding-right: 25px;
		}
		.experts-header{
			width: 100%;
			.page-title{
				flex-direction: row;
				padding-left: 0px;
			}
		}
	}
}
.expert-bios-tab{
	padding-right: 0px;
	padding-left: 0px;
	.tab-container{
		background-color: #f1f1f1;
		.tab {
			background-color: #f1f1f1;
			margin: 0 auto;
			overflow: hidden;
			margin-left: 165px;
			margin-right: 165px;
	
			@media (min-width: 1200px) and (max-width: 1440px){
				margin-left: calc( ( 100vw - 1150px) / 2);
			}
	
			@media (max-width: 1200px){
				margin-left: calc( ( 100vw - 970px) / 2);
			}
		
			button {
				background-color: inherit;
				float: left;
				border: none;
				outline: none;
				cursor: pointer;
				padding: 26px 50px;
				transition: 0.3s;
				font-size: 16px;
				font-weight: 500;
				&:hover {
					background-color: #e1e1e1;
				}
			
				&.active {
					background-color: $white;
					color: $primary-pink;
				}
			}
		}
	}

	.container{
		position: relative;
		.arrows{
			cursor: pointer;
			position: absolute;
			top: 50%;
		}
		.prev{
			left: 14px;
		}
		.next{
			right: 14px;
		}
	}

	
	.tabcontent {
		display: none;
		padding: 50px 15px;
		border-top: none;
		animation: fadeEffect 1s;
	}
	.active{
		display: block!important;
	}
	.hidden{
		display: none;
	}

	@keyframes fadeEffect {
		from {opacity: 0;}
		to {opacity: 1;}
	}

	.doctor-profile{
		div{
			//Mobile
			@media(max-width: $mobile){
				align-items: center;
			}
		}
		.doc-mobile{
			//Mobile
			@media(max-width: $mobile){
				padding: 0;
			}
		}
		img{
			max-width: 100%;
			margin-right: 0;
			//Mobile
			@media(max-width: $mobile){
				margin: 0;
			}
		}
		.call-to-action{
			a{
				margin:0;
			}
		}
	}

	//Mobile
	@media(max-width: $mobile){
		display: none!important;
	}
}

.expert-bios-tab-xs{
	display: none;
	//background: linear-gradient(#303248 25%, #FFFFFF 0%);
	//Mobile
	@media(max-width: $mobile){
		display: block;
	}
	.doctor-profile {

		// Mobile
		@media(max-width: $mobile){
			img {
				position: relative;
				top: 0;
			}
			
			h1 {
				line-height: 1em;
			}
		}
	}
}
/*------------ END EXPERT -----------*/

