/*---------- SEARCH PAGE ---------------*/
.search-page{
	padding-top: 50px;
	//Tablet
	@media (max-width: $tablet){
		padding-top: 30px;	
	}
	
	.sub-menu{
		display: none;
	}

	.blog-posts-preview-container, 
	.thumb-slider, 
	.app-review-container{
		h1{
			font-size: 30px;
			color: $black;
			font-weight: $regular;
			//Tablet
			@media (max-width: $tablet){
				font-size: 20px !important;	
			}
			//Mobile
			@media (max-width: $tablet){
				font-size: 23px !important;	
			}		
		}
		.slider-container{
			.slick-initialized{
				.slick-slide{
					display: flex;
				}
			}
		}
		.btn-load{
			background-color: $white;
			border-color: $border-color;
			font-weight: $regular;
			color: $black;
			font-size: 18px;
			text-align: center;
			border-radius: 21px;
			padding: 10px 50px;
		}
	}
	.filter{
		a{
			h2{
				font-size: 20px;
				color: $baby-blue;
				text-decoration: underline;
				margin-bottom: 0;
				&.active{
					color: $primary-pink;
				}
			}
		}
	}

	.search-header-form{
		width: 100%;
		border-bottom: 1px solid $border-color;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 60px;
		//Tablet
		@media (max-width: $tablet){
			width: 97%;
		}
		@media (max-width: $mobile){
			padding: 0;
		}
		h1{
			display: inline-block;
			vertical-align: middle;
			border-bottom: none!important;
			padding: 40px 0;
			//Tablet
			@media (max-width: $tablet){
				font-size: 25px;
			}
			//Mobile
			@media (max-width: $mobile){
				font-size: 22px;
				padding: 20px 0;
			}
		}
		form{
			display: inline-block;
			vertical-align: middle;
			input{
				border: none;
				font-size: 2em;
				//Tablet
				@media (max-width: $tablet){
					font-size: 25px;
				}
				//Mobile
				@media (max-width: $mobile){
					font-size: 22px;
				}
				&::placeholder {
					color: $border-color;
				}
			}
		}
		.search-results{
			display: flex;
			align-items: center;

		}
	}
	.search-navbar{
		display: none;
		ul{
			display: flex;
			flex-direction: row;
			justify-content: center;
			li{
				border: 1px solid $border-color;
				border-radius: 50px;
				cursor: pointer;
				margin: 20px 5px;
				padding: 5px 20px;
				-webkit-border-radius: 50px;
				-moz-border-radius: 50px;
				-ms-border-radius: 50px;
				-o-border-radius: 50px;
			}
			.active{
				border: 1px solid $primary-pink;
				background-color: $primary-pink;
				color: $white;
			}
			.border-none{
				border: 0px;
			}
		}
		//Mobile
		@media (max-width: $mobile){
			ul{
				flex-direction: column;
				li{
					padding: 20px 10px;
					margin: 0;
					border: 0;
					border-bottom: 1px solid $border-color;
					border-radius: 0;
					-webkit-border-radius: 0px;
					-moz-border-radius: 0px;
					-ms-border-radius: 0px;
					-o-border-radius: 0px;

				}
				.active{
					background-color: transparent;
					border: 0;
					border-bottom: 1px solid $border-color;
					color: $primary-pink;
				}
				.refine{
					display: none;
				}
			}
		}
	}
	.blog-posts-preview-container{
		.header{
			align-items: center;
			.filter{
				a{
					display: block;
				}
			}
		}
		.slider-container{
			position: relative;
			.slick-dots{
				position: absolute;
				right: 0;
				top: -52px;
				//Mobile
				@media (max-width: $mobile){
					display: none !important;
				}

			}
		}
	}

	.thumb-slider{
		.title{
			margin-top: 50px;
			text-align: left;
		}
		.slick-dots{
			position: absolute;
			top: -47px;
			right: 0;
		}
		.slider-container{
			.prev, .next{
				top: 35%;
			}
			.blog-thumbnail-slide{
				.trim{
					position: relative;
				}
				h2{
					font-weight: $regular;
				}
				//Mobile
				@media (max-width: $mobile){
					display: flex;
					.trim{
						width: 50%;
						max-height: 92px;
						img{
							width: 100%;
						}
					}
					h2{
						margin-left: 0%;
						padding-left: 4%;
						padding-top: 0;
						width: 50%;
					}
					.play-video-btn{
						top: 45% !important;
						right: 7% !important;
						@media screen and (min-width:0\0) { 
							/* Styles for IE9, IE10, IE11  */
							img{
								height: 100%;
							}
						}
					}
				}
			}
			.slick-list{
				.slick-slide{
					.play-video-btn{
						top: 120px;
						width: 58px;
						height: 58px;
						//Tablet
						@media (max-width: $tablet){
							top: 80px;
							width: 42px;
							height: 42px;
						}
						img{
							width: 14px;
							//Tablet
							@media (max-width: $tablet){
								width: 12px;
							}
						}
					}
				}
			}
		}
	}
	.app-review-container{
		padding: 0;
		.slider-container{
			position: relative;
			padding-bottom: 50px;
			.slick-arrow{
				position: absolute;
				top: 19%;
				cursor: pointer;
				transition: transform .5s;
				transform: translate(0, -50%);

				&.prev{
					left: -30px;
				}
				&.next{
					right: -30px;
				}
			}
			.slick-dots{
				position: absolute;
				right: 0;
				top: -52px;
			}

			.app-post-preview{
				//Tablet
				@media (max-width: $tablet){
					margin: 10px;
				}
				.footer{
					h3{
						color: $baby-blue;
						font-size: 20px;
						text-decoration: underline;
						//Tablet
						@media (max-width: $tablet){
							font-size: 18px;
						}
						//Mobile
						@media (max-width: $mobile){
							font-size: 16px;
						}	
					}
				}
				.app-post-content{
					margin: 0;
				}
			}
		}
	}
	.no-results{
		border-bottom: 1px solid $border-color;
		padding-top: 50px;
		padding-bottom: 50px;
		position: relative;
		text-align: center;
		width: 100%;
		h3{
			font-size: 2rem;
		}
	}
}

