/*--------- UTILITIES ----------*/
.utilities{
	padding-top: 65px;
	margin-bottom: 50px;
	background-color: #f8f8f8;
	.left{
		border-right: 1px solid $border-color;
	}
	.content{
		margin: 0 60px;
		//mobile
		@media(max-width: $mobile){
			margin: 0 10px 50px;
		}
		.header{
			//mobile
			@media(max-width: $mobile){
				position: absolute;
				top: 0;
				left: 50%;
				transform: translate(-50%,0);
			}
			img{
				width: 175px;
				//mobile
				@media(max-width: $mobile){
					width: 120px;
				}
			}
		}
		.body{
			padding: 35px 0;
			//mobile
			@media(max-width: $mobile){
				padding: 140px 0 42px 0;
			}
			h2{
				font-size: 35px;
				font-weight: $light;
				margin-bottom: 15px;
				// Tablet
				@media(max-width: $tablet){
					font-size: 30px;
				}
				// Mobile
				@media(max-width: $mobile){
					font-size: 25px;
					padding-left: 10px;
					padding-right: 10px;
				}
			}
		}
		.footer{
			margin-bottom: 30px;
			// Mobile
			@media(max-width: $mobile){
				position: absolute;
				bottom: 13px;
				left: 50%;
				transform: translate(-50%,0);
				a{
					display: none;
					background-color: $white;
				}
			}
			a{
				border: 1px solid $border-utilities-color;
				border-radius: 21px;
				color: $black;
				padding: 7px 22px;
			}
		}
	}
	.border-utilities{
		margin-top: 79px;
		border-bottom: 1px solid $border-color;
		border: none;
		// Tablet
		@media(max-width: $tablet){
			margin-top: 50px;
		}
	}
}
/*--------- END UTILITIES ----------*/