
/*--------- MAP ----------*/

.map-container	{
	background-color: $white;
	position: relative;
	padding: 50px 0;
	margin: 0;

	.container-header {
		width: 100%;
		margin: 0;
		.container{
			align-items: center;
			display: flex;
			justify-content: space-between;
			
			//Tablet
			@media(max-width: $tablet){
				padding: 25px 10px;
			}
			input{
				background-color: transparent;
				background-image: url(../img/icons/search-map.svg);
				background-position: 99%;
				background-repeat: no-repeat;
				background-size: 8%;
				border: none;
				border-bottom: 1px solid $black;
				height: 41px;
				padding: 5px 5px;
				width: 100%;
				-moz-appearance:none;
				-webkit-appearance:none;
				-ms-appearance:none;
				-o-appearance:none;
				appearance:none;
				cursor:pointer;
				border-radius: 0;
				padding-right: 35px;
			}
			input::-ms-expand {
				display: none;
			}

			//Mobile
			@media(max-width: $mobile){
				flex-direction: column;
			}
		}
		h1 {
			font-size: 30px;
			padding: 25px 0;

			//Tablet
			@media(max-width: $tablet){
				font-size: 23px;
				align-self: flex-start;
			}
		}
	}
	.map {
		width: 100%;
		height: 600px;
		position: relative;
		display: flex;

		//Tablet
		@media(max-width: $tablet){
			height: 450px;
		}

		iframe {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;

		}

	}

	.location-description {
		display: none;
		margin-left: auto;
		max-width: 500px;
		position: absolute;
		right: 165px;
		top: 150px;
		width: 30%;
		z-index: 99;

		@media (max-width: 1440px){
			right: 135px;
		}

		@media (min-width: 1200px) and (max-width: 1440px){
			right: calc( ( 100vw - 1120px) / 2);
		}

		@media (max-width: 1200px){
			right: calc( ( 100vw - 970px) / 2);
		}

		img {
			position: absolute;
			top: 25px;
			right: 25px;
			cursor: pointer;
		}
		.header {
			background: $primary-pink;
			padding: 50px 35px;
			color: $white;

			h1 {
				font-size: 26px;

			}

			p, a {
				font-size: 16px;
				color: $white;
				display: block;
				padding: 3px;
			}
		}
		.call-to-actions {
			background-color: $white;
			padding: 0 25px;
			a, p {
				display: block;
				color: $primary-pink;
				border-bottom: 1px solid $border-color;
				padding: 15px 0;
				font-weight: $medium;
				&:last-child {
					border-bottom: none;
				}
			}
			p {
				color: $black!important;
				display: block;
				padding: 15px 0;
				font-weight: $medium;
			}
		}
	}

	.locations {
		padding: 50px 0 100px 0;
		border-bottom: 1px solid $border-color;

		//Tablet
		@media(max-width: $tablet){
			padding: 50px 25px 100px 25px;
		}
		
		//Mobile
		@media(max-width: $mobile){
			border-bottom: none;
			padding-top: 0;
			padding-bottom: 0;
		}
		
		.location {
			
			a{
				width: 50%;
				float: left;
				display: flex;
				padding-top: 25px;
				cursor: pointer;

				//Tablet
				@media(max-width: $tablet){
					padding: 25px;
				}
			
				//Mobile
				@media(max-width: $mobile){
					width: 100%;
					padding: 25px 0;
					border-bottom: 1px solid $border-color;
				}
				span {
					background: $border-color;
					width: 40px;
					height: 40px;
					color: $white;
					border-radius: 100%;
					font-size: 24px;
					font-weight: $medium;
					display: flex;
					justify-content: center;
					padding-top: 5px;
				}
				.number {
					display: flex;
					flex-direction: column;
					align-self: flex-start;
					margin: 0 20px;
				}
				.direction {
					border-bottom: 1px solid $border-color;
					padding-bottom: 20px;

					//Mobile
					@media(max-width: $mobile){
						border: none;
					}
					
					h2 {
						font-size: 18px;
						color: $black;
						font-weight: $medium;
					}

					p {
						padding-top: 5px;
						color: $black;
						font-size: 14px;
						font-weight: $light;
					}

				}
			}
			.invisible{
				display: none!important;
			}
		}
		.active {
			span {
				background: $primary-pink!important;
			}

			h2, p, a {
				color: $primary-pink!important;
			}

			p {
				font-weight: $medium!important;
			}
		}
	}

	.autocomplete{
		width: 300px;
		margin-bottom: 20px;
		//Mobile
		@media(max-width: $mobile){
			width: 100%;
		}
	}
}

.ui-autocomplete{
	background-color: transparent;
	border: 0 !important;

	a{
		padding: 10px !important;
		display: block !important;
		&.ui-state-focus{
			border: 0 !important;
			background: $light-grey !important;
			margin: 0 !important;
			
		}
	}
}


/*--------- END MAP ----------*/