
/*--------- THUMB SLIDER ----------*/

.thumb-slider {
	position: relative;
	margin: 50px 0;

	// Mobile
	@media(max-width: $mobile){
		margin: 15px 0;
	}
	> div {
		
		> h1 {
			// color: $primary-pink;
			font-size: 32px;
			// font-weight: $light;
			text-align: left;
			padding-left: 16px;
			//padding-top: 50px;
			margin-bottom: 20px;

			//Tablet 
			// @media(max-width: $tablet){
			// 	text-align: center;
			// }

			//Mobile
			@media(max-width: $mobile){
				font-size: 26px;
				text-align: left;
			}
		}
	
		p{
			text-align: left;
			padding: 0px 10px 50px 10px;
			width: 85%;
			font-size: 16px;
			font-weight: $light;

			//Tablet 
			@media(max-width: $tablet){
				text-align: center;
				margin: auto;
			}

			//Mobile
			@media(max-width: $mobile){
				text-align: left;
				margin-left: 0;
				width: 100%;
			}
		}
	}
	&.left-title {
		>h2 {
			font-size: 30px;
			color: $black;
			padding-bottom: 25px;
			margin-left: 10px;
			font-weight: $medium;
			text-align: left;
		}
	}

	.slider-container {
		position: relative;
		.slick-list{
			//Mobile
			@media (max-width: $mobile){
				padding-top: 0px;
			}
			.slick-slide{
				display: flex;
				justify-content: center;
				outline: none;
				&:hover {
					cursor: pointer;
				}
				
				&:focus {
					outline: none;
				}
			}
		}
		.slick-dots{
			li{
				//Mobile
				@media (max-width: $mobile){
					margin: 0 0.8rem;
				}
			}
		}
		.prev {
			position: absolute;
			left: -30px;
			top: 26%;
			cursor: pointer;
			transition: transform .5s;
			&:hover {
				transform: scale(1.1);
			}
		}

		.slides {
			// Slider GRID
			&.slides_grid{
				//Mobile
				@media (max-width: $mobile){
					.blog-thumbnail-slide{
						display: flex;
						justify-content: center;
						.trim{
							width: 50%;
							position: relative;
						}
						h2{
							width: 45%;
							margin-left: 5%;
							padding-top: 0;
						}
						.blog-thumbnail-bg{
							background-size: cover;
						}
					}
				}
			}

			.blog-thumbnail, .blog-thumbnail-slide, .playlist-video{
				display: block;
				position: relative;
				.play-video-btn{
					top: 86px;
					height: 46px;
					width: 46px;
					right: 14px;
					position: absolute;
					//Tablet
					@media (max-width: $tablet){
						top: 45px;
						right: 10px;
						width: 36px;
						height: 36px;
					}
					img{
						width: 11px;
						//Mobile
						@media (max-width: $tablet){
							width: 9px;
						}
						@media screen and (min-width:0\0) { 
							/* Styles for IE9, IE10, IE11  */
							height: 100%;
						}
					}
				}
				.play-video-btn-lg{
					right: 6% !important;
				}
				&.post-slider-provisorio{
					.trim{
						position: relative;				
						.play-video-btn{
							top: auto;
							bottom: 10px;
						}
					}
				}
			}

			//Mobile
			@media (max-width: $mobile) {
				display: flex;
				flex-direction: column;
			}
		}
	
		.next {
			@extend .prev;
			right: -30px;
			left: auto;
		}

		&.four-slides {
			.blog-thumbnail, .blog-thumbnail-slide, .playlist-video {
				h2 {
					font-size: 18px;
				}
			}
		}

		//Mobile 
		@media(max-width: $mobile){
			.prev {
				left: -14px;
				display: none !important;
			}

			.next {
				left: auto;
				right: -14px;
				display: none !important;
			}
		}
	}
}

/*--------- END THUMB SLIDER ----------*/


/*--------- BLOG THUMBNAIL ----------*/

.blog-thumbnail, .blog-thumbnail-slide, .playlist-video {
    display: block;
	margin: 0 15px;
	
	//Mobile 
	@media(max-width: $mobile){
		margin: 10px 15px;
	}

	.trim{
		max-height: 200px;
		overflow: hidden;
		img {
			width: 100%;
		}
		//Mobile
		@media(max-width: $mobile){
			max-height: 95px;
		}
    }

    .post-info {
        display: flex;
        justify-content: space-between;
        span {
            color: $turquoise;
            display: block;
            margin: 15px 0 5px 0;
            font-weight: $semibold;
        }
    }
	h1{
		padding-top: 15px;
	}
    h2 {
        color: $black;
        font-size: 16px !important;
        padding-top: 14px;
        line-height: 1.5em;
        display: block;
		font-weight: $regular;
    }
}
/*--------- END BLOG THUMBNAIL ----------*/