/*------------ VIDEO PLAYLIST ---------------*/
.playlist-container{
    padding: 25px 0;

    .sponsors {
        // Tablet
        @media(max-width: $tablet){
            margin: 0;
        }
        // Mobile
        @media(max-width: $mobile){
            margin: 0;
        }
    }
    .video-playlist{
        background-color: transparent;
        padding: 0 15px;
        .description{
            padding: 0 27px 0 12px !important;
            // Mobile
            @media(max-width: $mobile){
                padding: 0 30px;
                margin-top: 15px;
            }
            span{
                color: $turquoise;
            }
            h1{
                color: $primary-pink;
                margin-left: -3px;
            }
            p{
                color: $black;
            }
        }
        .video-bg{
            padding: 0;
            .video-module-playlist{
                background-color: $black;
                margin: 35px 0;
            }
        }
        .playlist-text{
            color: $black;
            font-weight: 500;
            font-size: 18px;
            margin: 20px 0 -20px 0px;
        }
    }
    .playlist-video{
        .play-video-btn{
            cursor: pointer;
        }
        .content{
            margin-top: 15px;
            h1{
                padding-top: 5px;
            }
            h2{
                color: $teal;
                padding-top: 0;
            }
        }
        //Mobile
        @media (max-width: $mobile){
            iframe, .blog-thumbnail-bg {
                width: 50%;
                float: left;
            }
            .content{
                width: 45%;
                float: right;
                margin-top: 0;
                h1{
                    padding-top: 0;
                }
            }
            .play-video-btn{
                top: 47px !important;
                right: 54% !important;
            }
        }
    }
    .thumb-slider{
        margin-bottom: 0;
        @media (max-width: 767px){
            .next,.prev{
                display: none!important;
            }
        }
    }
}