/*----------- Fonts -------------*/


@font-face {
    font-family: NeufileGrotesk;
    src: url(../fonts/neufile/neufile-grotesk-light.otf);
    font-weight: 300;
}

@font-face {
    font-family: NeufileGrotesk;
    src: url(../fonts/neufile/neufile-grotesk-regular.otf);
    font-weight: 400;
}

@font-face {
    font-family: NeufileGrotesk;
    src: url(../fonts/neufile/neufile-grotesk-medium.otf);
    font-weight: 500;
}

@font-face {
    font-family: NeufileGrotesk;
    src: url(../fonts/neufile/neufile-grotesk-semibold.otf);
    font-weight: 600;
}

$primary-font: 'NeufileGrotesk', sans-serif;

$light :300;

